@import "../index.css";

.input-field {
    min-height: 7rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: 3rem;
    letter-spacing: 0.1rem;
    color: var(--white);
    background-color: var(--screen_bg);
    border: none;
    border-radius: 0.5rem;
    padding: 0.3rem 1.5rem 0.3rem;
    margin-bottom: 1rem;
}

.keypad {
    background-color: var(--toogle_keypad_bg);
    max-width: 30rem;
    min-height: 25rem;
    border-radius: 0.5rem;
    display: grid;
    grid-template: repeat(5, 1fr) / repeat(4, 1fr);
    gap: 1.5rem;
    padding: 1.5rem;
}

.btn {
    font-family: inherit;
    font-size: inherit;
    border: none;
    border-radius: 0.5rem;
    background-color: var(--ligrorange_key_bg);
    color: var(--dark_grayish_blue);
    box-shadow: 0 0.2rem var(--grorange_key_shadow);
    cursor: pointer;
    user-select: none;
}

.btn-reset:hover, 
.btn-reset:focus,
.btn-del:hover,
.btn-del:focus {
    filter: brightness(120%);
}

.btn-reset {
    font-size: 1.6rem;
    grid-column: 1 / 3;
    background-color: var(--darkblue_key_bg);
    color: var(--white);
    box-shadow: 0 0.2rem var(--darkblue_key_shadow);
}

.btn-del {
    font-size: 1.6rem;
    background-color: var(--darkblue_key_bg);
    color: var(--white);
    box-shadow: 0 0.2rem var(--darkblue_key_shadow);
}

.btn-eql {
    font-size: 1.6rem;
    grid-column: 3 / -1;
    background-color: var(--red_key_toggle_bg);
    color: var(--white);
    box-shadow: 0 0.2rem var(--darkred_key_shadow);
}