@import "../index.css";

.header {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 1rem;
}

.logo {
    color: var(--white);
    padding-left: 0.3rem;
}

.theme-container {
    margin-left: auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 1rem;
    padding-right: 0.3rem;
}

.theme-title {
    color: var(--white);
    font-size: 0.6rem;
    letter-spacing: 0.05rem;
}

.theme-header {
    color: var(--white);
    font-size: 0.5rem;
    display: flex;
    justify-content: space-around;
}

#toggle-btn {
    appearance: none;
    -webkit-appearance: none;
    width: 3rem;
    height: 0.8rem;
    background-color: var(--toogle_keypad_bg);
    border-radius: 1rem;
    cursor: pointer;
    padding: 0.6rem 0.1rem;
}