* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
    min-height: 100vh;
    max-width: 100%;
    overflow-x: hidden;
    font-family: 'League Spartan', sans-serif;
    font-size: 2rem;
    background-color: var(--main_bg);
}

:root {

  /* Theme 1 */

  /* Backgrounds */
  --main_bg: hsl(222, 26%, 31%);
  --toogle_keypad_bg: hsl(223, 31%, 20%);
  --screen_bg: hsl(224, 36%, 15%);

  /* Keys */
  --darkblue_key_bg: hsl(225, 21%, 49%);
  --darkblue_key_shadow: hsl(224, 28%, 35%);

  --red_key_toggle_bg: hsl(6, 63%, 50%);
  --darkred_key_shadow: hsl(6, 70%, 34%);

  --ligrorange_key_bg: hsl(30, 25%, 89%);
  --grorange_key_shadow: hsl(28, 16%, 65%);

  /* Text */
  --dark_grayish_blue: hsl(221, 14%, 31%);
  --white: hsl(0, 0%, 100%);

}